<template>
    <div class="classify-all-out">
        <div class="classify-all">
            <div :class="{on:item===foodClassify[id]}" class="classify-item" v-for="(item,index) in foodClassify"
                 @click="changeBgd(index)">{{item}}

                <div class="item-line" :class="{onLine:item===foodClassify[id]}"></div>
            </div>
        </div>
        <div class="classify-content">


            <!--背景色设置-->
            <div class="content-item " v-for="(item,index) in foodClassify" :class="{bgm:item===foodClassify[id]}"

            >
                <div class="content-name" ref="element">
                    <div class="item-line1"></div>
                    {{item}}
                </div>
                <div class="content-info" v-for="(items,indexs) in foodsDetail[index]">
                    <img class="img-info"
                         :src="items.image"
                         alt="">
                    <div class="info-out">
                        <div class="info-name">{{items.name}}</div>
                        <div class="info-number">
                            <span>月销{{items.sellCount}}</span>
                            <span>好评率{{items.rating}}%</span>
                        </div>
                        <div class="info-money">￥{{items.price}}</div>
                    </div>
                    <div class="img-right">

                        <!--减少-->
                        <transition name="fade">
                            <img v-show="items.num" class="img2 img-same"
                                 @click="handleReduce(items,foodsDetail[index])"
                                 src="http://lgting.oss-cn-beijing.aliyuncs.com/wx-img/jianshao.png" alt="">
                        </transition>
                        <span v-show="items.num" class="number-count">{{items.num}}</span>

                        <!--添加-->
                        <img @click="handleAdd(items,foodsDetail[index])" class="img1 img-same"
                             src="http://lgting.oss-cn-beijing.aliyuncs.com/wx-img/tianjia.png"
                             alt=""></div>
                    <div class="item-line2" v-show="indexs!==(foodsDetail[index].length-1)">

                    </div>

                </div>

            </div>
        </div>
        <div class="classify-foot">
            <div class="gouwuche-out">
                <div class="gouwuche">
                    <img class="icon-shopping_cart" v-show="!numArr.length"
                         src="http://lgting.oss-cn-beijing.aliyuncs.com/wx-img/gouwuche.png"></img>
                    <img v-show="numArr.length" class="icon-shopping_cart"
                         src="http://lgting.oss-cn-beijing.aliyuncs.com/wx-img/gouwuche-full.png"></img>
                    <span v-show="numArr.length" class="butCount">{{numData}}</span>
                </div>
            </div>
            <span class="foot-total">￥{{totalAll}}元</span>
            <span>另需配送费￥4元</span>
            <span class="min-money">￥20元起送</span>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Commodity",
        data() {
            return {
                totalAll: 0,
                buyCount: 0,
                numArr: [],
                numData: 0,
                id: 0,
                height: 0
            }
        },
        computed: {
            foodClassify() {
                return this.$store.getters.foodClassify
            },
            foodsDetail() {
                return this.$store.getters.foodsDetail
            },
            changeHeight(el) {
                console.log(el)
            }


        },
        methods: {
            changeBgd(index) {
                this.id = index
                console.log(this.$refs.element.offsetTop)
            },
            handleReduce(items, data) {
                if (items.num >= 1) {
                    items.num--;
                    this.totalAll -= items.price;
                    this.numData--;
                    if (this.totalAll == 0) {
                        this.numArr = data.filter(item => item.num >= 1)

                    }
                } else {

                }
            },
            handleAdd(items, data) {
                items.num++;
                let totalSingle = 0;
                let numSingle = 0;
                this.numArr = data.filter(item => item.num >= 1)
                this.numArr.forEach((item) => {
                    totalSingle += item.num * item.price
                    numSingle += item.num
                })
                this.totalAll = totalSingle
                this.numData = numSingle
                console.log(numSingle)
            }
        },
        mounted() {
            console.log(this.$refs.element.offsetTop)
        }
    }
</script>

<style scoped>

    .classify-all-out {
        width: 100%;
        overflow: hidden;

    }

    .classify-all {
        width: 80px;
        background: #f3f5f7;
        position: fixed;
        /*top: 184px;*/
        bottom: 48px;
        overflow: scroll;
        top: 427px;
    }

    .classify-all::-webkit-scrollbar {
        display: none;
    }

    .classify-item {
        padding: 19px 8px 0px 10px;

        font-size: 13px;
    }

    .item-line {
        width: 80%;
        border-bottom: 1px solid #EAEBED;
        padding: 18px 1px 0px;
    }

    .classify-foot {
        background: #121C27;
        width: 100%;
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        height: 49px;
        color: whitesmoke;
        font-size: 12px;
    }

    img {
        width: 30px;
        height: 30px;
    }

    .foot-total {
        font-size: 16px;
    }

    .min-money {
        display: inline-block;
        float: right;
        padding: 16px 23px;
        background: #00BF40;
    }

    .gouwuche-out {
        display: inline-block;
        text-align: center;
        width: 55px;
        height: 55px;
        background: #121C27;
        border-radius: 50%;
        position: relative;
        top: -8px;
        margin-left: 16px;

    }

    .gouwuche {
        display: inline-block;
        background: #3C3F41;
        position: relative;
        top: 7px;
        width: 45px;
        height: 45px;
        border-radius: 50%;
    }

    .content-info .img-info {
        width: 60px;
        height: 60px;
    }

    .gouwuche img {
        position: relative;
        margin-top: 6px;
    }

    .classify-foot span {
        display: inline-block;
        height: 49px;
        line-height: 49px;
        padding: 0px 15px;

    }

    .classify-content {
        position: fixed;
        width: 100%;
        /*top: 184px;*/
        bottom: 48px;
        left: 80px;
        overflow: scroll;
        top: 427px;
    }

    .content-item {
        position: relative;
    }

    .classify-content::-webkit-scrollbar {
        display: none;
    }

    .content-info {
        padding: 13px 17px;
    }

    .img-info {
        width: 60px;
        height: 60px;
    }

    .info-out {
        display: inline-block;
        height: 68px;
    }

    .content-name {
        font-size: 14px;
        color: gray;
        padding: 5px 20px;
        background: #EAEBED;
    }

    .item-line1 {
        width: 2px;
        height: 29px;
        margin-left: -19px;
        margin-top: -5px;
        background: gray;
        display: inline-block;
        float: left;
    }

    .info-name {
        font-size: 15px;
        font-weight: 700;
        /*width: 60%;*/
    }

    .info-number {
        width: 100%;
    }

    .img-same {
        width: 25px;
        height: 25px;

    }

    .img2-out span {
        padding: 0px 10px;

    }

    .img2 {
        margin-top: 1px;
        width: 22px;
        height: 22px;
        vertical-align: top;
        margin-right: -3px;
    }

    .info-number span {
        color: gray;
        font-size: 12px;
        display: inline-block;
        margin-right: 11px;
        /*vertical-align: top;*/
    }

    .info-money {
        color: red;
        font-size: 14px;
        width: 60%;
    }

    .img2-out {
        display: inline-block;
        height: 30px;
        position: relative;
        right: -70px;
        top: -48px;
        float: right;
        margin-right: 70px;
    }

    .item-line2 {
        height: 1px;
        background: gray;
        margin: 0 110px 0 17px;
        margin: 0 87px 0 0px;
    }

    .info-number .number-count {
        color: gray;
        font-size: 19px;
        display: inline-block;
        float: right;
        padding: 0px 7px;
        vertical-align: middle;
        margin-right: 0;
    }

    .img-right {
        display: inline-block;
        /* width: 8%; */
        margin-right: 87px;
        vertical-align: top;
        margin-top: 21px;
        float: right;
    }

    .img-right span {
        vertical-align: top;
        margin-top: 3px;
        margin-right: -5px;
        display: inline-block;
        width: 19px;
        text-align: center;
        font-size: 15px;
        color: gray;
    }

    .gouwuche .butCount {
        vertical-align: top;
        display: inline-block;
        position: absolute;
        background: red;
        height: 12px;
        text-align: center;
        margin-top: -6px;
        margin-left: -14px;
        color: #fff;
        border-radius: 5px;
        padding: 2px 9px;
        line-height: 12px;
    }

    .fade-enter-active, .fade-leave-active {
        transition: opacity 1s ease-out;
    }

    .fade-enter, .fade-leave-to {
        opacity: 0;
    }

    .on {
        background: white;
        border-bottom: 10px;
    }

    .onLine {
        opacity: 0;
    }

    .bgm {

        background: #fff19c;

        /*top: -210px;*/
    }
</style>


